<template>
  <div class="contanier">
    <div class="row" v-if="showStatment" >
      <AccountStatement-Table :AccountId="AccountId.id" :FromDate="FromDate" :ToDate="ToDate" class="flex md12 sm12 xs12"/>
    </div>
    <div class="row" v-if="!showStatment" style="justify-content: center;" >
      <va-card :title="$t('tables.labelCutomerChoose')" class="flex md8 sm12 xs12">
        <form @submit.prevent="okCreatefun()">
          <div class="row flex-center">
            <div class="flex md6 sm12 xs12 px-1">
              <va-date-picker
                :label="$t('forms.dateTimePicker.from')"
                v-model="FromDate"
              />
            </div>
            <div class="flex md6 sm12 xs12 px-1">
              <va-date-picker
                :label="$t('forms.dateTimePicker.to')"
                v-model="ToDate"
              />
            </div>
            <div class="flex md4 sm12 xs12 px-1">
              <va-select
                :label="$t('placeholder.AccountName')"
                v-model="fKAccount.value"
                required
                searchable
                textBy="name"
                keyBy="id"
                :options="fKAccount.options"
              />
            </div>
          </div>

          <div class="d-flex justify--center mt-3">
            <va-button type="submit" :disabled="stopBtn" class="my-0">{{ $t('modal.ok') }} </va-button>
          </div>
        </form>
      </va-card>
    </div>
  </div>
</template>

<script>
import AccountStatementTable from './AccountStatement/AccountStatementTable'
import AccountsService from '../../../services/finances/AccountsService'
import helper from '../../../helper/helper'

export default {
  name: 'AccountStatement',
  components: {
    AccountStatementTable,
  },
  data () {
    return {
      fKAccount: {
        value: '',
        options: [],
      },
      AccountId: 0,
      FromDate: '',
      ToDate: '',
      showStatment: false,
      stopBtn: false,
    }
  },
  created () {
    this.getCustomer()

    if (this.FromDate === null || this.FromDate === undefined || this.FromDate === '') {
      this.FromDate = helper.todayDate()
    }
  },
  methods: {
    getCustomer () {
      AccountsService.getAll()
        .then(response => {
          this.fKAccount.options = response.data
        },
        err => {
          console.error(err)
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    okCreatefun () {
      this.stopBtn = true
      if ((this.FromDate !== null && this.FromDate !== undefined && this.FromDate !== '')) {
        if ((this.fKAccount.value !== null && this.fKAccount.value !== undefined && this.fKAccount.value !== '')) {
          this.AccountId = this.fKAccount.value
        }
        this.showStatment = !this.showStatment
      } else {
        this.stopBtn = false
        this.showToast(this.$t('generic.errorChoose'), {
          icon: 'fa-exclamation',
          position: 'bottom-right',
          duration: 1500,
          type: 'error',
        })
      }
    },
  },
}

</script>

<style lang="scss">
  .row-equal .flex {
    .va-card {
      height: 100%;
    }
  }

  .dashboard {
    .va-card {
      margin-bottom: 0 !important;
    }
  }

</style>
