<template>
  <va-card :title="$t('tables.labelAccountStatement')" class="w-100">
    <va-data-table
      :fields="fields"
      :data="dataTable"
      :loading="loading"
      no-pagination
      hoverable
    >
      <template slot="marker" slot-scope="props">
        {{props.rowIndex + 1}}
      </template>
      <template slot="date" slot-scope="props">
        {{ formatDate(props.rowData.date)}}
      </template>
      <template slot="name_AccountStatements_Type" slot-scope="props">
        {{ props.rowData.restrictions_Type.name}}
      </template>
      <template slot="name_AccountStatements_State" slot-scope="props">
        {{ props.rowData.restrictions_State.name}}
      </template>
    </va-data-table>
    <div class="row " >
      <div class="flex md4 xs12">
        <p class="display-5">{{$t('generic.totalDepit')}}</p>
        <p>
          {{totalDepit}}
        </p>
      </div>
      <div class="flex md4 xs12">
        <p class="display-5">{{$t('generic.totalCredit')}}</p>
        <p>
          {{totalCredit}}
        </p>
      </div>
      <div class="flex md4 xs12">
        <p class="display-5">{{$t('generic.total')}}</p>
        <p>
          {{total}}

        </p>
      </div>

    </div>
  </va-card>
</template>

<script>
import AccountStatementService from '../../../../services/finances/AccountsService'
import helper from '../../../../helper/helper'
export default {
  props: {
    AccountId: Number,
    FromDate: String,
    ToDate: String,
  },
  data () {
    return {
      search: {},
      dataTable: [],
      loading: true,
      currentobject: {},
      total: 0,
      totalDepit: 0,
      totalCredit: 0,
    }
  },

  computed: {
    fields () {
      return [{
        name: '__slot:marker',
        width: '30px',
        dataClass: 'text-center',
        title: '#',
      }, {
        name: 'sumDepit',
        dataClass: 'text-center',
        title: this.$t('tables.headings.dept'),
      },
      {
        name: 'sumCredit',
        dataClass: 'text-center',
        title: this.$t('tables.headings.credit'),
      }, {
        name: 'id',
        dataClass: 'text-center',
        title: this.$t('tables.headings.restrictionNumber'),
      }, {
        name: '__slot:date',
        title: this.$t('tables.headings.date'),
      },
      {
        name: '__slot:name_AccountStatements_Type',
        title: this.$t('tables.headings.name_Restrictions_Type'),
      },

      {
        name: '__slot:name_AccountStatements_State',
        title: this.$t('tables.headings.name_Restrictions_State'),
      },
      {
        name: 'notes',
        title: this.$t('tables.headings.notes'),
      }]
    },
  },
  watch: {
    dataTable: function (val) {
      this.totalCredit = 0.0
      this.totalDepit = 0.0
      this.total = 0.0
      this.dataTable.forEach(element => {
        this.totalCredit = parseFloat(this.totalCredit + element.sumCredit)
        this.totalDepit = parseFloat(this.totalDepit + element.sumDepit)
        this.total = Math.abs(parseFloat(this.totalDepit - this.totalCredit))
      })
    },
  },
  created () {
    this.readItems()
  },
  methods: {
    riquierdvalidation (data) {
      return helper.riquierdvalidation(data)
    },
    numircvalidation (data) {
      return helper.numircvalidation(data)
    },
    numircriquierdvalidation (d) {
      return helper.numircriquierdvalidation(d)
    },

    /*  api Padination */
    readItems () {
      this.loading = true
      const params = {
        // PageSize: this.perPage,
        // PageNumber: page,
        CompanyActivity: 1,
        id_account: this.AccountId === 0 ? null : this.AccountId,
        DateFrom: this.FromDate,
        DateTo: this.ToDate,
      }
      AccountStatementService.getAllAccountStatement(params)
        .then(response => {
          this.dataTable = response.data
          this.loading = false
        },
        err => {
          console.error(err)
          this.loading = false
          this.showToast(this.$t('generic.error'), {
            icon: 'fa-exclamation',
            position: 'bottom-right',
            duration: 1500,
            type: 'error',
          })
        })
    },
    formatDate (dateString, FullDate = true) {
      if (FullDate) {
        const options = { weekday: 'short', year: 'numeric', month: 'numeric', day: 'numeric', hour: 'numeric', minute: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      } else {
        const options = { year: 'numeric', month: 'numeric', day: 'numeric' }
        return new Date(dateString).toLocaleDateString('en-GB', options)
      }
    },

  },
}

</script>

<style lang="scss">
  i.fa.fa-exclamation {
    color: red !important;
  }

</style>
