import http from '../../http-common'

class Accountsercice {
  getAll () {
    return http.get('Accounts')
  }

  getAllPagnation (params) {
    return http.get('Accounts', { params })
  }

  getAllAccountStatement (params) {
    return http.get('GetAllAccountStatment', { params })
  }

  GetAllTreeAcc () {
    return http.get('GetAllTreeAcc')
  }

  GenerateCode (id, IsMain) {
    return http.get(`GenerateCode?Id=${id}&Is_main=${IsMain}`)
  }

  get (id) {
    return http.get(`Accounts/getById?Id=${id}`)
  }

  getAllFinalAccounts () {
    return http.get('GetAllFinalAccounts')
  }

  create (data) {
    return http.post('Accounts', data)
  }

  update (id, data) {
    return http.put(`Accounts?Id=${id}`, data)
  }

  delete (id) {
    return http.delete(`Accounts?Id=${id}`)
  }
}

export default new Accountsercice()
